<template>
  <div class="edit-sku contain">
    <el-row>
      <!--<el-col :span="2">-->
      <!--<el-button @click="goBackHistory" type="primary">返回</el-button>-->
      <!--</el-col>-->
      <el-col :span="22">
      <h1 class="left title">{{ form.id==''?'新增':'编辑'}}SKU</h1>
      </el-col>
    </el-row>

    <el-form :model="form" label-width="250px">

      <el-form-item label="Sku">
        <el-input v-model="form.sku" :disabled="!(form.id == 0)"></el-input>
      </el-form-item>
      <el-form-item label="重量(g)">
        <el-input v-model="form.weight"></el-input>
      </el-form-item>
      <el-form-item label="长度(cm)">
        <el-input v-model="form.length"></el-input>
      </el-form-item>
      <el-form-item label="宽度(cm)">
        <el-input v-model="form.width"></el-input>
      </el-form-item>
      <el-form-item label="高度(cm)">
        <el-input v-model="form.height"></el-input>
      </el-form-item>
      <el-form-item label="进货价(以人民币元为单位)">
        <el-input v-model="form.buy_price"></el-input>
      </el-form-item>
      <el-form-item label="国内端运费(以人民币元为单位)">
        <el-input v-model="form.domestic_logistics_price"></el-input>
      </el-form-item>
      <el-form-item label="包装成本(以人民币元为单位)">
        <el-input v-model="form.package_price"></el-input>
      </el-form-item>


      <el-form-item label="物流方式" style="text-align: left">
        <!--<el-radio-group v-model="form.logistics_type">-->
          <!--<el-radio label="1">直邮</el-radio>-->
          <!--<el-radio label="2">FBA</el-radio>-->
        <!--</el-radio-group>-->

        <el-cascader
                :options="logisticOption"
                v-model="logistics_type"
                expand-trigger="hover"
        >
        </el-cascader>


      </el-form-item>


      <el-form-item label="特殊属性" style="text-align: left">
        <el-checkbox-group v-model="type">
          <el-checkbox label="1" name="type">电子产品</el-checkbox>
          <el-checkbox label="2" name="type">粉末</el-checkbox>
          <el-checkbox label="3" name="type">液体</el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item>
        <el-button type="success" @click="onSubmit">保存</el-button>
        <el-button type="danger" @click="goBackHistory">取消</el-button>
        <el-button type="info">成本试算</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
export default {
  name: 'editSku',
  data () {
    return {
        logisticOption:[],
        logistics_type:[],
//        form:{
//          id:''
//          sku:'',
//          weight: "",
//          length: "",
//          width: "",
//          height: "",
//          buy_price: "",
//          domestic_logistics_price: "",
//          package_price: "",
//          logistics_type: "",
//          type:[]
//        },
        type:[],
    }
  },
    computed:{
        ...mapState({
            form:state=>state.manager.editSkuData
        })
    },
  methods:{
      onSubmit(){
          let type = this.type.join(',')
          let url='';
          if(this.form.id == ''){
              url = '/home/skuDetail/save';
          }else{
              url = '/home/skuDetail/update'
              this.form.id = this.sku_id;
          }
          this.form.logistics_type = this.logistics_type[2];
          this.$http.post(url,
              {
                  ...this.form,
                  type
              }
          )
          .then((res)=>{
            if (res.body.status == 1){
                //success

                this.$notify({
                    title: '成功',
                    message: '保存成功,正在跳转...',
                    type: 'success'
                });
                setTimeout(function () {
                  this.$router.push({path:'/index/sku'})
                }.bind(this),1500)
            }else{
                this.$notify({
                    title: '失败',
                    message: res.body.message,
                    type: 'error'
                });
            }
          })
      },
      goBackHistory(){
          this.$router.go(-1);
      },
      getLogisticsData(){
          this.$http({
              url:'/home/logistics/getLogisticsData'
          }).then(res=>{
              console.log(res)
              if(res.body.status == 1){
                  console.log(res.body.data)
                  this.logisticOption = res.body.data


              }
          })
      },
  },
  mounted(){
    this.getLogisticsData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .edit-sku .title{
    font-size: 1.5em;
    margin:20px;
  }
</style>



